/* ============================================================
 CUSTOM STYLE OVERRIDES FOR ART GALLERY
 This file loads AFTER all of al-folio's main theme CSS.
============================================================ */

/* ---------- FILTER BUTTONS ---------- */
#filter-buttons {
 margin-bottom: 1.5rem;
 display: flex;
 gap: 0.5rem;
 flex-wrap: wrap;
}

#filter-buttons button {
 padding: 0.4rem 0.9rem;
 border: none;
 border-radius: 20px;
 background: #ddd;
 cursor: pointer;
 font-size: 0.9rem;
 transition: background 0.2s ease;
}

#filter-buttons button:hover {
 background: #ccc;
}

#filter-buttons button.active {
 background: #8a9b68;
 color: white;
}

/* ---------- MASONRY GRID ---------- */
.masonry-grid {
 column-count: 3;
 column-gap: 1.5rem;
}

@media (max-width: 1000px) {
 .masonry-grid {
 column-count: 2;
 }
}

@media (max-width: 600px) {
 .masonry-grid {
 column-count: 1;
 }
}

.masonry-item {
 break-inside: avoid-column;
 margin-bottom: 1.5rem;
}

/* ---------- IMAGE ---------- */
.img-wrapper {
 overflow: hidden;
 border-radius: 6px;
 margin-bottom: 0.5rem;
}

.img-wrapper img {
 width: 100%;
 height: auto;
 display: block;
 transition: transform .3s ease, opacity .3s ease;
 cursor: zoom-in;
}

.img-wrapper:hover img {
 transform: scale(1.05);
 opacity: 0.9;
}

/* ---------- LIGHTBOX ---------- */
#lightbox {
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background: rgba(0,0,0,0.85);
 display: none;
 justify-content: center;
 align-items: center;
 z-index: 5000;
 padding: 1rem;
}

#lightbox img {
 max-width: 90%;
 max-height: 90%;
 border-radius: 6px;
 cursor: zoom-out;
 box-shadow: 0 0 20px rgba(0,0,0,0.4);
}
